import React from "react";
import cssVar from "views/mobileView/styles";
import ReadMoreText from "./ReadMoreText";
import { AiFillRightCircle, AiOutlineRight } from 'react-icons/ai'
import MintboxIcon from "./MintboxIcon";
import AMCIcon from "./AMCIcon";
import { CurrencyFormat } from "./CurrencyFormat";
import { baseImgURL } from "assets/Constant";
export default function SliderComponent({ navigation, type, fundType, section }) {
    
    const box_card = (slide,i) => {
        let headerColor = slide.headingColor
        let textColor = slide.contentColor
        let backgroundColor = slide.backgroundColor;
        let buttonTextColor = slide.labelColor
        let buttonBorderColor = slide.buttonColor;

        let buttonBackgroudColor = "#fff";
        if(slide.buttonType === 'contained'){
            buttonBackgroudColor = slide.buttonColor;
        }
        
        
        return (<div key={i}
            style={{
                marginRight: "4%",
                padding: "4%",
                border: "1px solid " + cssVar.getPropertyValue("--light"),
                borderRadius: '8px',
                backgroundColor: backgroundColor,
                display: "inline-flex",
                flexDirection: "column",
                width: section.sectionChildren?.length === 1 ? "96%" : "85%",
                whiteSpace: "normal"
            }}
        >
            <div className='header1dark' style={{ marginBottom: "4%", color: headerColor }}>
                {slide.heading}
            </div>
            <div className='bodyTextLight' style={{ marginBottom: "4%", lineHeight: "21px", color: textColor, width: "70%" }}>
                {slide.content}
            </div>
            <div style={{ display: "flex" }}>
                <div style={{ width: "70%", display: "flex", alignItems: "flex-end" }}>
                    <button className="outlineCardButton"
                        style={{
                            border: '1px solid ' + buttonBorderColor,
                            color: buttonTextColor,
                            backgroundColor: buttonBackgroudColor,
                        }}
                        onClick={() => {
                            if (slide.onClick) {
                                slide.onClick()
                            }
                            else if (slide.ActionTarget !== '') {
                                var a = JSON.parse(slide.webURL)
                                navigation.push(a)
                            }
                        }}
                    >
                        {slide.buttonText}
                    </button>
                </div>
                <div style={{ width: "30%", position: "relative", textAlign: 'right' }}>
                    <img style={{ maxWidth: "100%", height: "70px", position: 'absolute', right: 0, bottom: 0 }} src={slide.iconURL || baseImgURL+'SVG12.svg'} alt="" />
                </div>
            </div>
        </div>)
    }
    const reason_slider = (slide,i) => {
        return (<div key={i}
            style={{
                marginRight: "4%",
                border: "1px solid " + slide.backgroundColor,
                borderRadius: '8px',
                backgroundColor: cssVar.getPropertyValue('--lightest'),
                display: "inline-flex",
                flexDirection: "column",
                alignItems:'center',
                width: "90%",
                whiteSpace: "normal",
            }}
        >
            <div style={{display:'flex', minHeight:'145px'}}>
            <div style={{flex:1,padding:'4% 0 4% 4%'}}>
                <div className='header1dark' style={{ marginBottom: "4%" }}>
                    {slide.heading}
                </div>
                <div className='bodyTextLight' style={{ marginBottom: "4%", lineHeight: "21px" }}>
                    {slide.content}
                </div>
            </div>
            <div style={{ backgroundColor: slide.backgroundColor, textAlign:'center',display:'flex',borderTopLeftRadius:'50%',borderBottomLeftRadius:'50%', alignItems:'center',padding:'4% 2% 4% 8%', borderTopRightRadius:'6px', borderBottomRightRadius:'6px' }}>
                <img style={{ height: "64px",width:'64px' }} src={slide.iconURL} alt="" />
            </div>
            </div>
        </div>)
    }
    const recently_viewed = (slide,i) => {
        return (<div
            key={i}
            onClick={() => {
                if (slide.type === 1) {
                    navigation.push("/ETF-detail/" + slide.ID)
                } else if (slide.type === 2) {
                    navigation.push("/fund-detail/" + slide.ID)
                } else if (slide.type === 3) {
                    navigation.push("/mintbox-detail/" + slide.ID)
                }
            }}
            style={{
                marginRight: "4%",
                padding: "4%",
                border: "1px solid " + cssVar.getPropertyValue("--light"),
                borderRadius: '8px',
                backgroundColor: cssVar.getPropertyValue("--white"),
                display: "inline-flex",
                flexDirection: "column",
                width: section.sectionChildren?.length === 1 ? "96%" : "80%",
                minHeight: slide.type === 3 ? "160px" : "140px",
                whiteSpace: "normal",
            }}
        >
            <div style={{ display: "flex", justifyContent: 'space-between', marginBottom: "4%" }}>
                {
                    slide.type === 3 ?
                        <MintboxIcon id={slide.MintboxId} height='40px' width='40px' />
                        :
                        <AMCIcon amcName={slide.AMCName} height='40px' width='40px' />
                }
                {
                    slide.type === 1 ?
                        <></>
                        :
                        <div>
                            <div className="smallTextLight" style={{ border: "1px solid " + cssVar.getPropertyValue('--light'), padding: "4px 8px", borderRadius: "4px", whiteSpace: "nowrap" }}>
                                <img src={baseImgURL+'coin.webp'} style={{ width: "12px", height: "12px", marginRight: "6px" }} />Min investment {CurrencyFormat(slide.MinInv, 'long', 2)}
                            </div>
                        </div>
                }
            </div>
            <div style={{ display: "flex", marginBottom: "3%", gap: '4%' }}>
                <div className="header2dark lineClamp" style={{ flex: '1' }}>
                    {slide.Name}
                </div>
                <div>
                    <div className="smallTextLight">5Y CAGR</div>
                    <div className="header2dark" style={{ color: cssVar.getPropertyValue("--secondaryGreen") }}>{Number(slide.XIRR).toFixed(2) + " %"}</div>
                </div>
            </div>
            <div className="smallTextLight lineClamp">
                {
                    slide.type === 1 || slide.type === 2 ?
                        slide.Category
                        :
                        slide.type === 3 ?
                            slide.About
                            :
                            ''
                }
            </div>
        </div>)
    }
    const fund_card = (slide, fundType, i) => {
        return (<div
            key={i}
            onClick={() => {
                if (fundType === 1) {
                    navigation.push("/ETF-detail/" + slide.Symbol)
                } else if (fundType === 2) {
                    navigation.push("/fund-detail/" + slide.FundCode)
                } else if (fundType === 3) {
                    navigation.push("/mintbox-detail/" + slide.MintboxId)
                }
            }}
            style={{
                marginRight: "4%",
                padding: "4%",
                border: "1px solid " + cssVar.getPropertyValue("--light"),
                borderRadius: '8px',
                backgroundColor: cssVar.getPropertyValue("--white"),
                display: "inline-flex",
                flexDirection: "column",
                width: "80%",
                minHeight: "160px",
                whiteSpace: "normal",
            }}
        >

            <div style={{ display: "flex", justifyContent: 'space-between', marginBottom: "4%" }}>
                {
                    fundType === 3 ?
                        <MintboxIcon id={slide.MintboxId} height='40px' width='40px' />
                        :
                        <AMCIcon amcName={slide.AMCName} height='40px' width='40px' />
                }
                <div>
                    <div className="smallTextLight" style={{ border: "1px solid " + cssVar.getPropertyValue('--light'), padding: "4px 8px", borderRadius: "4px", whiteSpace: "nowrap" }}>
                        <img src={baseImgURL+'coin.webp'} style={{ width: "12px", height: "12px", marginRight: "6px" }} />Min investment {CurrencyFormat(slide.MinSIPAmount)}
                    </div>
                </div>
            </div>
            <div style={{ display: "flex", marginBottom: "3%", gap: '4%' }}>
                <div className="header2dark lineClamp" style={{ flex: '1' }}>
                    {fundType === 1 ? slide.ETFName : fundType === 2 ? slide.FundName : fundType === 3 ? slide.MintboxName : ""}
                </div>
                <div>
                    <div className="smallTextLight">5Y CAGR</div>
                    <div className="header2dark" style={{ color: cssVar.getPropertyValue("--secondaryGreen") }}>{Number(slide.XIRR).toFixed(2) + " %"}</div>
                </div>
            </div>
            <div className="smallTextLight lineClamp">
                {
                    fundType === 1 || fundType === 2 ?
                        slide.Category
                        :
                        fundType === 3 ?
                            slide.About
                            :
                            ''
                }
            </div>
        </div>)
    }
    const video_card = (slide, allVideos, i) => {
        return (
            // <a key={i} href={`https://www.youtube.com/watch?v=${slide.videoId}`} target="_blank">
            <a key={i} onClick={()=>navigation.push({pathname:'/video-player',state:{allVideos, selectedVideo:i}})}>
                <div
                    style={{
                        marginRight: "4%",
                        borderRadius: '8px',
                        display: "inline-block",
                        width: section.sectionChildren?.length === 1 ? "96%" : "85%",
                        whiteSpace: "normal",
                        border: "1px solid "+cssVar.getPropertyValue('--light')
                    }}>
                    <div style={{ position: "relative" }}>
                        <img
                            src={`https://img.youtube.com/vi/${slide.videoId}/maxresdefault.jpg`}
                            style={{
                                paddingVertical: 10,
                                width: "100%",
                                borderRadius: 6,
                            }} />
                        <img
                            src='https://cdn4.iconfinder.com/data/icons/logos-and-brands/512/395_Youtube_logo-512.png'
                            style={{
                                position: 'absolute',
                                width: "40px",
                                bottom: 8,
                                right: 16,
                            }} />
                    </div>
                </div>
            </a>
        )
    }
    const blog_card = (slide, i) => {
        return (
            <a key={i} href={slide.webURL} target='_blank' rel="noreferrer">
                <div
                    style={{
                        width: "85%",
                        marginRight: "4%",
                        display: "inline-block",
                        whiteSpace: "normal",
                        overflow: "hidden"
                    }}>
                    <img
                        src={slide.iconURL}
                        style={{
                            height: "162px",
                            width: "100%",
                            borderRadius: '8px',
                            marginBottom: '2%'
                        }} />
                    {
                        slide.heading.split(':').map((h,i) => <div key={i} className="header2dark" style={{ 
                            display: '-webkit-box',
                            WebkitBoxOrient: 'vertical',
                            WebkitLineClamp: 1,
                            overflow: 'hidden',
                            textOverflow: 'ellipsis',}}>{h}</div>)
                    }
                    <div className='header3dark' style={{ color: cssVar.getPropertyValue("--secondaryGreen"), marginTop: "1%" }}>
                        Read blog <AiOutlineRight size={8} />
                    </div>
                </div>
            </a>
        );
    }
    const small_box = (slide, i) => (

        <div
            key={slide.UID}
            style={{
                borderRadius: 6, border: "1px solid " + cssVar.getPropertyValue("--light"),
                backgroundColor: slide.bgColor, padding: 16, width: 120, minHeight: 128,
                marginRight: "4%", display: "inline-flex", flexDirection: "column", whiteSpace: "normal"
            }}
            onClick={() => slide.onClick()}
        >
            <div style={{ fontSize: 14, flex: "1" }}>{slide.heading}</div>
            <AiFillRightCircle
                size={20}
                style={{
                    color: slide.btnColor,
                    alignSelf: "flex-start",
                }}
            />
        </div>

    )

    if (section && section.sectionChildren && section.sectionChildren.length !== 0) {
        return (
            <div style={{ margin: "4% 0" }}>
                {section.sectionHeading !== '' ?
                    <div style={{ margin: "4% 4% 0 4%", display: 'flex', alignItems: 'center', gap: "4%" }}>
                        <div className='header1dark' style={{ flex: 1 }}>{section.sectionHeading}</div>
                        {
                            section.sectionRight !== '' ?
                                <div style={{ margin: 'auto 0 auto 10px' }} onClick={() => navigation.push(section.sectionRightURL)}>{section.sectionRight}</div>
                                :
                                <></>
                        }
                    </div>
                    :
                    <></>
                }
                {section.sectionContent !== '' ?
                    <div className='bodyTextLight' style={{ margin: "1% 4% 0 4%" }} >
                        <ReadMoreText text={section.sectionContent} />
                    </div>
                    :
                    <></>
                }
                <div className="hiddden-scrollbar" style={{ paddingLeft: "4%", marginTop:'4%' }}>
                    {type === "videoView" && section.sectionChildren.map((slide, i) => (
                        video_card(slide,section , i)
                    ))}
                    {type === "blogView" && section.sectionChildren.map((slide, i) => (
                        blog_card(slide, i)
                    ))}
                    {type === "boxCard" && section.sectionChildren.map((slide, i) => (
                        box_card(slide, i)
                    ))}
                    {type === "recentlyViewed" && section.sectionChildren.map((slide, i) => (
                        recently_viewed(slide, i)
                    ))}
                    {type === "smallBox" && section.sectionChildren.map((slide, i) => (
                        small_box(slide, i)
                    ))}
                    {type === "fundCard" && section.sectionChildren.map((slide, i) => (
                        fund_card(slide, fundType, i)
                    ))}
                    {type === "reasonSlider" && section.sectionChildren.map((slide, i) => (
                        reason_slider(slide, i)
                    ))}
                </div>
            </div>
        )
    }
    else {
        return <></>
    }
}

